import { useState } from 'react';

import ExpenseForm from './NewExpenseForm';
import './NewExpense.css';

const NewExpense = (props) => {
  const [isEditing, setIsEditing] = useState(false);
  const saveExpenseDataHandler = (enteredExpenseData) => {
    const expenseData = {
      ...enteredExpenseData,
      id: Math
        .random()
        .toString()
    };
    props.onAddExpense(expenseData);
    setIsEditing(false);
  };

  const addNewExpenseHandler = () => {
    setIsEditing(true);
  };

  const stopEditExpenseHandler = () => {
    setIsEditing(false);
  };
  return (
    <div className="new-expense">
      {!isEditing && <button onClick={addNewExpenseHandler}>Add New Expense</button>}
      {isEditing && <ExpenseForm onCancel={stopEditExpenseHandler} onSaveExpense={saveExpenseDataHandler} />}
    </div>
  );
};

export default NewExpense;